function renderBase() {
  const body = document.querySelector('body');

  body.innerHTML = `
    <header>

    <form class="search-input-form">
      <button class="search-button"><i class="fas fa-search"></i></button>
      <input id="search" class="search-input" placeholder="Enter your search...">
    </form>
    
    </header>

    <section class="my-vid">
      <div class="slider-wrapper video-panel">
        <ul class="video-list slider">
        </ul>
      </div>
    </section>

    <footer class="pagination-wrapper slider-wrapper hidden">
            
      <button id="left-button" title="10 pages back">
        <i class="fa fa-fast-backward" aria-hidden="true"></i>
      </button>
            
      <div class="pagination-panel slider-wrapper">
        <ul class="slider pagination-panel-ul">
        </ul>
      </div>

      <button id="right-button" title="10 pages forward">
        <i class="fa fa-fast-forward" aria-hidden="true"></i>
      </button>

    </footer>
    `;
}

module.exports = renderBase;
