const key = 'AIzaSyD18E18KO7fwIjwXtCuPOow0yL_FYtDQs4';
const downloadVideo = 20;
let nextPage = '';

function requestXMLHttp() {
  const inputValue = document.querySelector('#search').value;
  const url = `https://www.googleapis.com/youtube/v3/search?key=${key}&type=video&part=snippet&maxResults=${downloadVideo}&q=${inputValue}&pageToken=${nextPage}`;

  return fetch(url)
    .then(response => response.json())
    .then((incomingData) => {
      const data = incomingData;
      const idForStatistics = data.items.map(eachElement => eachElement.id.videoId).join(',');
      const urlStatistic = `https://www.googleapis.com/youtube/v3/videos?key=${key}&id=${idForStatistics}&part=snippet,statistics`;

      nextPage = data.nextPageToken;

      return fetch(urlStatistic)
        .then(videosResponse => videosResponse.json())
        .then((videosData) => {
          for (let i = 0, len = data.items.length; i < len; i++) {
            data.items[i].statistics = videosData.items[i].statistics;
          }
          return data;
        });
    });
}

module.exports = requestXMLHttp;
