let videosOnThePage = 0;
let totalWellToPages = 0;


function arrangeTheCurrentPage(pageNumber) {
  if (pageNumber >= totalWellToPages) {
    return;
  }

  const width = document.querySelector('.video-panel').offsetWidth;

  document.querySelector('.video-list').style.marginLeft = `${-width * pageNumber}px`;

  if (pageNumber === 0 || pageNumber === 1 || pageNumber === 2) {
    document.querySelector('.pagination-panel-ul').style.marginLeft = `${-80}px`;
  } else {
    document.querySelector('.pagination-panel-ul').style.marginLeft = `${-40 * pageNumber}px`;
  }

  document.querySelector(`#page${pageNumber}`).checked = true;
}


function paginationFormat(currentPage) {
  videosOnThePage = document.querySelector('.video-panel').offsetWidth / 350;
  totalWellToPages = Math.floor(document.querySelector('.video-list').children.length / videosOnThePage);

  const paginationUl = document.querySelector('.pagination-panel-ul');

  paginationUl.innerHTML = '';

  for (let i = 0; i < totalWellToPages; ++i) {
    paginationUl.innerHTML += `
      <li class="pagination-panel-ul-li">
        <input id="page${i}" type="radio" name="page">
        <label for="page${i}"  title="${i + 1}">${i + 1}</label>
      </li>
    `;
  }
  if (currentPage === undefined) {
    let page = currentPage;
    page = Math.floor(-parseInt(document.querySelector('.video-list').style.marginLeft || 0, 10) / document.querySelector('.video-panel').offsetWidth);
    arrangeTheCurrentPage(page);
  }
  if (currentPage < totalWellToPages) {
    document.querySelector(`#page${currentPage}`).checked = true;
  }
}

module.exports = { paginationFormat, arrangeTheCurrentPage };
