import { GetCorrectDate } from './getCorrectDate';

function renderArticles(data, isRedraw) {
  const videoList = document.querySelector('.video-list');
  const getCorrectDate = new GetCorrectDate();

  if (isRedraw) {
    document.querySelector('.video-list').innerHTML = '';
  }

  data.items.forEach((item) => {
    videoList.innerHTML += `
      <li class="video">
        <a class="video-a" href="https://www.youtube.com/watch?v=${item.id.videoId}" target="_blank">
          <img class="video-a-img" src="${item.snippet.thumbnails.medium.url}">
        </a>
        <h2 class="video-h2">${item.snippet.title}</h2>
          <ul class="video-ul">
            <li class="video-ul-li">
              <span class="item-card-icon"><i class="fa fa-address-card-o" aria-hidden="true"></i></span>
              <span class="video-ul-li-author">${item.snippet.channelTitle}</span>
            </li>
            <li class="video-ul-li">
              <span class="item-card-icon"><i class="fa fa-calendar-check-o" aria-hidden="true"></i></span>
              <span class="video-ul-li-date">${getCorrectDate.convertion(item.snippet.publishedAt)}</span>
            </li>
            <li class="video-ul-li">
              <span class="item-card-icon"><i class="fa fa-heart" aria-hidden="true"></i></span>
              <span class="video-ul-li-like">${item.statistics.likeCount}</span>
            </li>
            <li class="video-ul-li">
              <span class="item-card-icon"><i class="fa fa-eye"></i></span>
              <span class="video-ul-li-view">Просмотры ${item.statistics.viewCount}</span>
            </li>
          </ul>
          <h3 class="video-h3">${item.snippet.description}</a>
          </h3>
      </li>
    `;
  });
}

module.exports = renderArticles;
