const requestXMLHttp = require('./requestXMLHttp');
const { paginationFormat, arrangeTheCurrentPage } = require('./pagination.js');
const renderArticles = require('./renderArticles');

let startSwipePosition;
let endSwipePosition;
let isSwipeStart = false;

function search() {
  document.querySelector('.search-input-form').addEventListener('submit', (e) => {
    e.preventDefault();
    requestXMLHttp()
      .then((data) => {
        renderArticles(data, true);
        document.querySelector('.pagination-wrapper').classList.remove('hidden');
        paginationFormat(0);
        arrangeTheCurrentPage(0);
      });
  });
}

function swipeStart(e) {
  if (e.target.nodeName === 'INPUT' || document.querySelector('.video-list').children.length === 0) {
    return;
  }
  isSwipeStart = true;
  startSwipePosition = e.clientX || e.targetTouches[0].clientX;
  e.preventDefault();
}

function swipeEnd(e) {
  if (!isSwipeStart) {
    return;
  }

  const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 1;

  endSwipePosition = e.clientX || e.changedTouches[0].clientX;

  const event = new Event('swipeEnd', {
    bubbles: true,
  });

  if (startSwipePosition - endSwipePosition > 10) {
    arrangeTheCurrentPage(pageNumber + 1);
    document.querySelector(`#page${pageNumber + 1}`).dispatchEvent(event);
  } else if (endSwipePosition - startSwipePosition > 10 && pageNumber > 0) {
    arrangeTheCurrentPage(pageNumber - 1);
    document.querySelector(`#page${pageNumber - 1}`).dispatchEvent(event);
  }

  isSwipeStart = false;
}

function swipeMouse() {
  document.querySelector('body').addEventListener('mousedown', swipeStart);
  document.querySelector('body').addEventListener('mouseup', swipeEnd);
}

function swipeTouch() {
  document.querySelector('body').addEventListener('touchstart', swipeStart);
  document.querySelector('body').addEventListener('touchend', swipeEnd);
}

function pagination() {
  document.querySelector('.pagination-panel-ul').addEventListener('change', (e) => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 1;

    arrangeTheCurrentPage(pageNumber);
    const event = new Event('swipeEnd', {
      bubbles: true,
    });
    e.target.dispatchEvent(event);
  });
}

function upload() {
  document.querySelector('.pagination-panel-ul').addEventListener('swipeEnd', (e) => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 1;

    if (pageNumber >= Math.floor(e.currentTarget.children.length * 0.8)) {
      requestXMLHttp()
        .then(data => renderArticles(data, false))
        .then(() => paginationFormat(pageNumber));
    }
  });
}

function resize() {
  function widthChange() {
    if (document.querySelector('.pagination-panel-ul').children.length > 0) {
      paginationFormat();
    }
  }

  if (matchMedia) {
    const mediaSmall = window.matchMedia('(max-width: 710px)');
    mediaSmall.addListener(widthChange);

    const mediaNormal = window.matchMedia('(max-width: 1070px)');
    mediaNormal.addListener(widthChange);

    const mediaBig = window.matchMedia('(max-width: 1440px)');
    mediaBig.addListener(widthChange);
  }
}

function buttons() {
  document.querySelector('#left-button').addEventListener('click', () => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 11;

    arrangeTheCurrentPage(pageNumber >= 0 ? pageNumber : 0);
  });

  document.querySelector('#right-button').addEventListener('click', () => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) + 9;
    const maxNumber = document.querySelector('.pagination-panel-ul').children.length - 1;

    arrangeTheCurrentPage(pageNumber < maxNumber ? pageNumber : maxNumber);

    const event = new Event('swipeEnd', {
      bubbles: true,
    });
    document.querySelector('.pagination-panel-ul').dispatchEvent(event);
  });
}

function listen() {
  search();

  swipeMouse();
  swipeTouch();

  pagination();
  upload();
  resize();

  buttons();
}

module.exports = listen;
